@import url(https://fonts.googleapis.com/css2?family=Oswald&family=Poppins&display=swap);
* {
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  overflow-x: hidden;
}
body span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
img {
  object-fit: cover;
  height: 50%;
  margin: 0;
}
.images-grid {
  flex-wrap: wrap;
  max-width: 70%;
}
.profile-paper {
  margin: 10px auto;
  width: 530px;
  padding: 40px;
}
.post-image {
  height: 88vh;
  width: 100%;
  object-fit: cover;
  object-position: center;
  position: relative;
}
.card-media {
  width: 100%;
  min-height: 450px;
  position: relative;
  scrollbar-width: none;
}
.image {
  object-position: center;
  object-fit: cover;
  scrollbar-width: none;
  min-height: 90vh;
  width: 100%;
}
.swipable {
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  position: relative;
  object-fit: cover;
  overflow: hidden;
}

.stepper1,
.post-image-full {
  width: 400px;
  height: 40vh;
  object-position: center;
  object-fit: cover;
  margin: 0px auto;
  position: relative;
}
.num-of-likes {
  font-weight: bold;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding-left: 10px;
  margin-top: 10px;
}
.caption {
  padding-left: 10px;
  margin-top: 4px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.paper-upload {
  width: 500px;
  min-height: 150px;
  max-height: 1000px;
  margin: 10px auto;
  text-align: center;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.upload-div {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.17rem;
  text-align: left;
  padding: 10px;
}
.upload-illust,
.friendreq-illust {
  width: 90%;
  height: 300px;
}
.selected-image {
  width: 90%;
  height: 300px;
  margin: 0px auto;
  position: relative;
}
.stepper {
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  margin: 0;
  background: transparent;
  padding: 0;
}
.card-media {
  width: 100%;
  height: 300px;
  position: relative;
  scrollbar-width: none;
}
.image {
  object-position: center;
  object-fit: cover;
  scrollbar-width: none;
  height: 60vh;
  width: 100%;
}
.swipable {
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  position: relative;
  object-fit: cover;
  overflow: hidden;
}
.upload-btn-div {
  width: 100%;
  height: 60px;
  position: relative;
}
.friends-paper {
  width: 500px;
  max-height: 100vh;
  text-align: center;
  margin: 10px auto;
}
.friendreq-card {
  width: 100%;
  margin: 0px auto;
  text-align: left;
  padding: 0;
  height: 58px;
}
.friendreq-card button {
  font-size: 0.6rem;
}
.post-image-full-grid {
  margin-bottom: 10px;
}
.bottom-nav-link {
  width: 20px;
}
.bottom-nav {
  width: 20px;
  margin: 0px auto;
}
.unfriend-alert {
  width: 400px;
  margin: 0px auto;
}
.image-in-grid {
  max-height: 240px;

  position: relative;
  padding: 4px;
}

.image-in-grid img {
  margin: 0;
  height: 100%;
  width: 100%;
}
.likes-and-comments {
  position: absolute;
  background: rgb(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
  z-index: 0;
}
.comments-illust {
  width: 90%;
  height: auto;
}
.hover-likes {
  position: absolute;
  color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.image-in-grid:hover .likes-and-comments {
  display: block;
}
.heart-icon {
  z-index: 200;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.6;
  color: white;
  transform: scale(1);
  -webkit-animation: lub 0.3s linear;
          animation: lub 0.3s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  transition: 0.2s ease-in-out all;
}
@-webkit-keyframes lub {
  0% {
    transform: scale(1);
  }
  60% {
    transform: scale(2.4);
  }
  83% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(2.4);
  }
}
@keyframes lub {
  0% {
    transform: scale(1);
  }
  60% {
    transform: scale(2.4);
  }
  83% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(2.4);
  }
}

@media only screen and (max-width: 500px) {
  .profile-paper {
    width: 96%;
    padding: 20px;
  }
  .profile-paper h5 {
    font-size: 1.3rem;
  }

  .friends-paper {
    width: 100%;
  }
  .profile-paper p {
    font-size: 0.8rem;
  }
  .upload-illust {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }
  .friendreq-illust {
    width: 90%;
    object-fit: cover;
    height: auto;
  }
}
@media only screen and (max-width: 408px) {
  .stepper,
  .post-image-full {
    width: 100%;
  }
  .images-grid {
    max-width: 97%;
  }
  .image-in-grid {
    max-height: 120px;
  }
}
@media only screen and (max-width: 530px) {
  .paper-upload {
    width: 95%;
  }
  .selected-image {
    width: 100%;
  }
  .images-grid {
    max-width: 97%;
  }
}
@media only screen and (max-width: 600px) {
  .post-image-full-grid {
    max-width: 400px;
    margin: 0px auto;
  }
  .post-image-full {
    margin: 0px auto;
  }
  .images-grid {
    max-width: 90%;
  }
  .image-in-grid {
    max-height: 170px;
  }
}
@media only screen and (max-width: 400px) {
  .post-image-full-grid {
    max-width: 100%;
  }
  .unfriend-alert {
    width: 350px;
    margin: 0px auto;
  }
}

@media only screen and (max-width: 460px) {
  .images-grid {
    max-width: 100%;
  }
  .image-in-grid {
    height: 160px;
  }
}
@media only screen and (max-width: 306px) {
  .images-grid {
    max-width: 100%;
  }
  .image-in-grid {
    max-height: 96px;
  }
}
@media only screen and (max-width: 1028px) {
  .images-grid {
    max-width: 85%;
  }
}
@media only screen and (max-width: 922px) {
  .images-grid {
    max-width: 93%;
  }
}
@media only screen and (max-width: 846px) {
  .images-grid {
    max-width: 97%;
  }
  .image-in-grid {
    max-height: 200px;
  }
}
@media only screen and (max-width: 538px) {
  .image-in-grid {
    max-height: 170px;
    padding: 0;
  }
}
@media only screen and (max-width: 365px) {
  .image-in-grid {
    height: 120px;
  }
  .selected-image {
    width: 96%;
    height: 250px;
  }
  .upload-illust {
    height: 180px;
  }
}
@media only screen and (max-width: 306px) {
  .image-in-grid {
    height: 100px;
  }
}
@media only screen and (max-width: 350px) {
  .image {
    height: 45vh;
  }
  .card-media {
    height: 250px;
    margin: 0;
    width: 95%;
  }
  .friendreq-card p {
    font-size: 0.8rem;
  }
  .friendreq-card button {
    font-size: 0.62rem;
    padding: 3px 4px;
    min-width: 3rem;
    text-transform: capitalize;
  }
  .unfriend-alert p {
    font-size: 0.79rem;
  }
  .unfriend-alert {
    width: 100%;
    margin: auto;
  }
  .nav {
    height: 45px;
    vertical-align: middle;
    line-height: 30px;
  }
  .nav h5 {
    margin-top: 0px;
    font-size: 1.4rem;
  }
  .friends-paper h6 {
    font-size: 0.9rem;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
@font-face {
  font-family: "my-font";
  src: url(/instaclone/static/media/Billabong.52b04f1c.ttf) format("truetype");
}
.register-paper input[type="text"],
input[type="password"],
input[type="number"] {
  height: 40px;
  padding: 0px 14px;
  padding-top: 2px;
  font-size: 0.9rem;
}

.register-paper {
  height: auto;
  margin: 34px auto;
  width: 304px;
}
.insta-title {
  font-family: "my-font";
  font-size: 1.8rem;
  letter-spacing: 0.04rem;
}
.login-title {
  font-family: "Oswald";
  font-size: 1.3rem;
  text-transform: uppercase;
}
.register-title {
  font-family: "Oswald";
  font-size: 1.3rem;
  text-transform: uppercase;
  margin-bottom: 10px;
}
@media only screen and (max-width: 500px) {
  body {
    overflow-x: hidden;
  }
}
.search-results {
  position: absolute;
  width: 256px;
  max-height: 130px;
  top: 0;
  left: 0;
  overflow: hidden;
  background: white;
}
.search-grid {
  background: pink;
  position: relative;
}
.wrapper {
  width: 500px;
  max-height: 400px;
  margin: 50px auto;
  position: relative;
}
.wrapper button {
  text-transform: capitalize;
  padding: 4px 6px;
}

.title {
  font-family: "my-font";
  width: auto;
  text-align: center;
  margin-bottom: 0px;
  margin: 0px auto;
  font-size: 2.5rem;
}
.register-paper label {
  height: 36px;
  top: -6px;
  font-size: 0.9rem;
}
.swipable {
  width: 100%;
}
.paper2 {
  position: relative;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.paper2-form input[type="password"] {
  height: 40px;
  padding: 0px 14px;
  padding-top: 1px;
  font-size: 0.9rem;
}
.paper2-form {
  margin: 40px auto;
  height: auto;
  width: 364px;
  padding: 10px;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  align-items: center;

  text-align: center;
}
.wrapper button {
  margin: 0px 4px;
}
.paper2-inputs {
  text-align: left;
  width: 200px;
  margin: 0px auto;
}
.textwrapper {
  width: 90%;
  height: 70px;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.text-swipable {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.text-grid {
  height: 100vh;
  position: relative;
  background: url(/instaclone/static/media/bg.358bec32.png);
  background-size: cover;
  background-position: center;
}
.div1 {
  height: auto;
  background: transparent;
  color: white;
  margin-bottom: 0px;
  font-family: "Poppins";
}
.div2 {
  height: auto;
  margin-bottom: 10px;
  color: white;
  font-family: "Poppins";
}
.grid2 {
  height: 100vh;
  padding: 0;
  background: linear-gradient(45deg, #ff2cdf, #0014ff);
  border-left: none;
  outline: none;
  position: relative;
}
.love-by-saiteja {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
}
/* home image */
.empty-posts-img {
  width: 500px;
  margin-bottom: 10px;
}

@media only screen and (max-width: 600px) {
  .text-grid {
    height: 100px;
    background-position: top;
  }
  .text-grid h4 {
    font-size: 1rem;
  }
  .text-grid p {
    font-size: 0.8rem;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  }

  .div1 {
    margin: 0px auto;
    margin-bottom: 4px;
  }
  .text-swipable {
    width: 100%;
    height: 60px;
  }
  .div2 {
    margin-top: 0px;
    margin-bottom: 20px;
  }
  .empty-posts-img {
    width: 90%;
    margin: 0px auto;
    height: auto;
  }
  .wrapper {
    width: 100%;

    margin-top: 0px;
  }
  .register-paper {
    height: auto;
    width: 90%;
    margin: 10px auto;

    max-height: 400px;
  }
  .paper2 {
    width: 100%;
  }
  .paper2-form {
    width: 90%;
  }
}
@media only screen and (max-width: 600px) and (min-width: 388px) {
  .textwrapper {
    height: 70px;

    width: 330px;
    padding: 0;
  }
}

